body {
  margin: 0;
  font-family: "Nunito", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a,
a::after {
  text-decoration: none;
  color: #454444;
  font-size: 65%;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
}

nav ul {
  -webkit-padding-start: 0;
  padding-inline-start: 0;
  margin: 0;
}

#navBar2 {
  display: flex;
  justify-content: space-evenly;
  margin: 50px 0;
  position: static;
}

li {
  list-style-type: none;
  margin-right: 25px;
  margin-left: 25px;
}

.underline {
  border-bottom: 3px solid #46b893;
}

#navBar2 > li:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

#jumbotronStyle {
  border: 1px solid #797979;

  margin: auto;
  text-align: center;
  background-color: white;
  /* box-shadow: 5px 10px 8px #888888; */
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 30px;
  height: 100%;
  margin-bottom: 50px;
  font-family: "Nunito";
  width: 70%;
}

.N04TextInfoContent-body-12 {
  font-size: 50vw;
}

.MuiCardMedia-root {
  background-position: center top;
}

#trailblazer {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 2%;
  grid-gap: 70px;
  gap: 70px;
}

#trailblazer li {
  margin: 2% 1%;
  margin-left: -40px;
  grid-gap: 50px;
  gap: 50px;
}

#explanationTrailblazer {
  display: flex;
  justify-content: center;
  height: auto;
}

#jumbotronStyle h2 {
  font-size: 32px;
  text-align: center;
  font-family: "Nunito", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  font-weight: 300;
  /* line-height: 2;
  margin-bottom: 3em; */
}

.topHeader {
  color: #9e9e9e;
  display: block;
  font-size: 14px;
  margin-top: 4%;
  margin-bottom: -2%;
  text-align: center;
  letter-spacing: 2px;
}

.bodyJumbotron {
  color: #222;
  width: 60%;
  margin: 4% auto;
  font-size: 10%;
  text-align: center;
  line-height: 1.75;
}

