.bodyJumbotron {
  color: #222;
  width: 50%;
  margin: 4% auto;
  font-size: 1.2vw;
  text-align: center;
  line-height: 1.75;
}

span,
#NavOverride {
  color: #46b893;
}

/* Override the nav bar */
#welcomeBody a,
#NavOverride {
  text-decoration: none;
  font-size: 100%;
}

#welcomeBody a:hover,
#NavOverride:hover {
  text-decoration: underline;
}

.rowInline {
  display: flex;
  height: 100%;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.customCard {
  border: 1px solid #797979;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 30px;
  font-family: "Nunito", sans-serif;
  width: 30%;
  height: 100%;
}

.button {
  border: 1px solid #797979;
  color: white;
  /* box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px; */
  border-radius: 10px;
  margin: 10% 10%;
  padding: 4% 4%;
  width: 70%;
  text-align: center;
}

/* The reason I'm targetting a tag is because we have to override the a tag styling from the nav bar... This can be improved */
#pitfallsButton:hover,
#trailblazersButton:hover,
#aboutButton:hover {
  background-color: black;
  /* background-color: #46b893; */
}
.customCard p {
  width: 70%;
  margin: auto;
  text-align: center;
  margin-bottom: 10%;
  color: #222;
  /* width: 67%;
  margin: 4% auto; */
  font-size: 1.2vw;
  text-align: center;
  line-height: 1.75;
}

.customCard h3 {
  margin: 13% 0 5% 0;
}
#trailblazersCard h3 {
  color: #4162aa;
}

#trailblazersButton a,
#pitfallsButton a,
#aboutButton a {
  color: white;
  font-size: 1.2vw;
}

#trailblazersButton {
  background-color: #4162aa;
}

#pitfallsCard h3 {
  color: #df6b50;
}

#pitfallsButton {
  background-color: #df6b50;
}

#aboutCard h3 {
  color: #54c7ee;
}

#aboutButton {
  background-color: #54c7ee;
}

img {
  text-align: center;
  margin: auto;
}

@media only screen and (max-width: 650px) {
  .rowInline {
    display: flex;
    height: 100%;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-evenly;
    text-align: center;
    margin: auto;
    align-items: center;
    grid-gap: 40px;
    gap: 40px;
  }
}

#wholeBody ul {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2em;
  margin-right: 15%;
  margin-left: 5%;
  width: 90%;
}
#wholeBody li {
  float: left;
  margin-right: 1%;
  margin-top: 2.5em;
  margin-bottom: 3em;
  list-style-type: none;
}
/* Responsivenes */
@media only screen and (max-width: 600px) {
  ul {
    display: flex;
    flex-direction: column;
  }
}

.ProfileContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
}

.OurStoryContainer {
  max-width: 860px;
  border: 1px solid #797979;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  margin: 20px auto;
  border-radius: 30px;
  padding: 60px 5px;
  box-sizing: border-box;
  background-color: #fff;
}

.OurStoryContainer h1 {
  color: #46b893;
  font-family: "Nunito", Helvetica, sans-serif;
  font-weight: 700;
  font-size: 30px;
  text-transform: capitalize;
  margin-top: 0;
}

.OurStoryContainer p {
  color: #454444;
  font-family: "Nunito", Helvetica, sans-serif;
  line-height: 32px;
  font-size: 18px;
  max-width: 660px;
  margin: 35px auto 0;
}

.TeamMemberContainer {
  max-width: 900px;
  margin: 100px auto;
}

.TeamMemberContainer h2 {
  color: #454444;
  font-family: "Nunito", Helvetica, sans-serif;
  font-weight: 700;
  font-size: 26px;
  text-transform: capitalize;
  max-width: 200px;
  margin: 0 auto 10px;
  border-bottom: 10px solid transparent;
  padding: 5px;
  border-image: url(/asianerasure.com/static/media/WaveUnderlinesDouble.805d3ff6.svg) 60 round;
}

.TeamMemberContainer h3 {
  font-family: "Playfair Display", Times, serif;
  font-weight: 700;
  font-size: 24px;
  text-transform: capitalize;
  max-width: 75px;
  margin: 0 auto 24px;
  border-bottom: 10px solid transparent;
  padding: 5px;
}

.TeamMemberContainer li:first-of-type h3 {
  border-image: url(/asianerasure.com/static/media/WaveUnderlineBlue.e4c18102.svg) 15 round;
}

.TeamMemberContainer li:nth-last-of-type(2) h3 {
  border-image: url(/asianerasure.com/static/media/WaveUnderlineOrange.3b253382.svg) 15 round;
}

.TeamMemberContainer li:last-of-type h3 {
  border-image: url(/asianerasure.com/static/media/WaveUnderlineNeonBlue.05c85809.svg) 15 round;
}

.TeamMemberContainer p {
  color: #454444;
  font-family: "Nunito", Helvetica, sans-serif;
  font-size: 18px;
  line-height: 25px;
}

.ProfileContainer:not(:last-of-type) {
  margin-bottom: 50px;
}

.BioContainerLeft {
  order: 1;
  background-color: #fff;
}
.BioContainerRight {
  order: 2;
  background-color: #fff;
}
.HeadshotContainerLeft {
  order: 1;
}
.HeadshotContainerRight {
  order: 2;
}

.App {
  text-align: center;
  color: black;
}
main,
body {
  text-align: center;
  color: black;
  background-image: url(/asianerasure.com/static/media/BackgroundPatterns.b14cce74.svg);
  background-repeat: repeat;
  background-color: rgba(255, 255, 255, 0.8);
  background-blend-mode: lighten;
  font-size: 20px;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

h1 {
  color: #46b893;
  text-transform: uppercase;
  display: inline-block;
  font-family: "Nunito", sans-serif;
  font-weight: 900;
  font-size: 160%;
  letter-spacing: 2px;
  text-align: center;
  margin-top: 35px;
  margin-bottom: -400px;
}

